import shape1 from '../../assets/images/widget/shape1.png';
import shape2 from '../../assets/images/widget/shape2.png';
import shape3 from '../../assets/images/widget/shape3.png';


import IntegrationScenarioNotif from '../../assets/images/user/avatar-1.jpg';
import WorkflowNotif from '../../assets/images/user/avatar-1.jpg';
import SystemNotif from '../../assets/images/user/avatar-1.jpg';

import PackagesMonitoring from '../../assets/images/packages_monitoring.svg';

import AmChartEarnings from "../Widget/Chart/AmChartEarnings";
import AmChartStatistics6 from "../Widget/Chart/AmChartStatistics6";




class Messages extends React.Component {
    state = {
        chatOpen: true,
        user: {
            id: 6,
        }
    };

    render() {
        let loader, ActionRight;
        let cardClass = [];

        if (this.state.loadCard) {
            cardClass = [...cardClass, 'card-load'];
            loader = (
                <div className="card-loader">
                    <i className="pct-loader1 anim-rotate"/>
                </div>
            );
        }
        if (this.props.cardClass) {
            cardClass = [...cardClass, this.props.cardClass];
        }

        const friendList = (friend).map(f => {
            return <Friend key={f.id} data={f} activeId={this.state.user.id} clicked={() => this.setState({chatOpen: true, user: f})} />;
        });

        let message = <div className="text-center text-danger">Chat Not Found</div>;
        chatMsg.filter(chats => {
            if (chats.friend_id === this.state.user.id) {
                message = (chats.messages).map((msg, index) => {
                    return <Message key={index} message={msg} name={this.state.user.name} photo={chats.friend_photo} />;
                });
            }
            return false;
        });

        return (
            <Aux>
                <Row>

                    <Col md={7}>
                        <NotificationsTabs></NotificationsTabs>
                    </Col>
                    <Col sm={5}>
                        <Row>
                        <Col sm={12}>
                            <h5 class="float-left mt-3">Requests by date</h5>
                        </Col>
                        <Col sm={12}>
                            <Card>
                                <Card.Body>
                                    <AreaChart1/>
                                </Card.Body>
                            </Card>
                        </Col>
                        <Col sm={12}>
                            <h5 class="float-left">Failure triggers</h5>
                        </Col>
                        <Col sm={12}>
                            <Card>
                                <Card.Body>
                                    <PieDoughnutChart/>
                                </Card.Body>
                            </Card>
                        </Col>
                        </Row>
                    </Col>



                    <IntegrationEvents></IntegrationEvents>

                    <WorkflowEvents></WorkflowEvents>


                </Row>
            </Aux>
        );
    }
}

export default Messages;